"use client"
import { useEffect, useMemo, useRef, useState } from 'react'
import { useCartStore } from '@/store/cart'

const ALL_ACCESSORIES = [
  { id: 'regleta', name: 'Regleta Termotronic y CBX', price: 12, tags: ['electrico','repuesto'], imgs: ['/images/accesorios/Regleta Termotronic y CBX/regleta.jpg','/images/accesorios/Regleta Termotronic y CBX/regleta003-1200x1200.jpg'] },
  { id: 'sensor_flujo_cbx2', name: 'Sensor de Flujo CBX2', price: 18, tags: ['sensor','agua'], imgs: ['/images/accesorios/Sensor De Flujo CBX2/Sensor Flujo.jpg','/images/accesorios/Sensor De Flujo CBX2/Sensor Flujo 0.jpg'] },
  { id: 'sensor_flujo_cbx3', name: 'Sensor de Flujo CBX3', price: 22, tags: ['sensor','agua'], imgs: ['/images/accesorios/Sensor De Flujo CBX3/SensorCBX3.jpg','/images/accesorios/Sensor De Flujo CBX3/SensorCBX300.jpg'] },
  { id: 'resistencia_termotronic', name: 'Resistencia Termotronic', price: 15, tags: ['resistencia'], imgs: ['/images/accesorios/Resistencia Termotronic/Resistencia.jpg','/images/accesorios/Resistencia Termotronic/Resistencia-W.jpg'] },
  { id: 'resistencia_cbx', name: 'Resistencia CBX', price: 15, tags: ['resistencia'], imgs: ['/images/accesorios/Resistencia CBX/Resistencia CBX.jpg','/images/accesorios/Resistencia CBX/Resistencia CBX 0.jpg'] },
  { id: 'resistencia_cbx3', name: 'Resistencia CBX3', price: 18, tags: ['resistencia'], imgs: ['/images/accesorios/Resistencia CBX3/ResistenciaCBX3.jpg','/images/accesorios/Resistencia CBX3/ResistenciaCBX3-4.jpg'] },
  { id: 'rele_cbx', name: 'Relé CBX', price: 10, tags: ['electrico'], imgs: ['/images/accesorios/Relé CBX/Rele 30 Amp.jpg','/images/accesorios/Relé CBX/Rele 30 Amp 02.jpg'] },
  { id: 'rele_ssr_cbx3', name: 'Relé SSR CBX3 40A', price: 25, tags: ['electrico'], imgs: ['/images/accesorios/Relé SSR CBX3/SSR-40amp.jpg','/images/accesorios/Relé SSR CBX3/SSR-40amp-0.jpg'] },
  { id: 'selector_temp_cbx', name: 'Selector de Temperatura CBX 3 Posiciones', price: 14, tags: ['selector'], imgs: ['/images/accesorios/Selector de Temperatura CBX de 3 Posiciones/Selector.jpg','/images/accesorios/Selector de Temperatura CBX de 3 Posiciones/Selector 0.jpg'] },
  { id: 'ntc_20k', name: 'Termistor NTC 20k', price: 6, tags: ['sensor'], imgs: ['/images/accesorios/Termistor NTC 20k/Termistor0.jpg','/images/accesorios/Termistor NTC 20k/Termistor00.jpg'] },
  { id: 'termostato_40a', name: 'Termostato 40A Termotronic DPST', price: 20, tags: ['termostato'], imgs: ['/images/accesorios/Termostato 40 Amp Termotronic DPST/Termost 50Amp.jpg','/images/accesorios/Termostato 40 Amp Termotronic DPST/Termost 50Amp 0.jpg'] },
]

export default function AccessoriesPage() {
  const addToCart = useCartStore(s => s.addItem)
  const [q, setQ] = useState('')
  const [tag, setTag] = useState('')
  const items = useMemo(() => ALL_ACCESSORIES.filter(a => (
    (!q || a.name.toLowerCase().includes(q.toLowerCase())) && (!tag || a.tags.includes(tag))
  )), [q, tag])

  return (
    <div className="container-edge py-14">
      <div className="flex items-end justify-between mb-6">
        <div>
          <h1 className="text-3xl font-semibold">Accesorios</h1>
          <p className="opacity-70">Explora repuestos y piezas de instalación.</p>
        </div>
        <div className="flex gap-2">
          <input placeholder="Buscar" value={q} onChange={e=>setQ(e.target.value)} className="rounded-md border border-border px-3 py-2" />
          <select value={tag} onChange={e=>setTag(e.target.value)} className="rounded-md border border-border px-3 py-2">
            <option value="">Todos</option>
            <option value="ac">AC</option>
            <option value="install">Instalación</option>
            <option value="mount">Soporte</option>
          </select>
        </div>
      </div>
      <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {items.map(i => (
          <div key={i.id} className="rounded-2xl border border-border overflow-hidden">
            <div className="relative aspect-square overflow-hidden bg-muted">
              {/* simple auto-advance slideshow */}
              <AccessorySlideshow images={i.imgs} />
            </div>
            <div className="p-4 space-y-1">
              <div className="flex items-center justify-between">
                <h3 className="font-medium">{i.name}</h3>
                <span className="text-sm">${i.price}</span>
              </div>
              <button className="btn btn-primary w-full mt-2" onClick={()=>addToCart(i)}>Add to Cart</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

function AccessorySlideshow({ images }: { images: string[] }) {
  const [idx, setIdx] = useState(0)
  const timer = useRef<ReturnType<typeof setInterval> | null>(null)
  useEffect(() => {
    if (images.length <= 1) return
    timer.current = setInterval(() => setIdx(i => (i + 1) % images.length), 2000)
    return () => { if (timer.current) clearInterval(timer.current) }
  }, [images])
  const current = images[idx]
  return (
    <img src={current} alt="Accessory" className="w-full h-full object-cover" />
  )
}


